/**
 * Queue Callback utilities for handling incoming webhook payloads from Vercel triggers
 */
import { QueueClient } from "./client";
import { Topic } from "./topic";
import type { MessageHandler } from "./types";
import { isDevMode, registerDevRouteHandler } from "./dev";

/**
 * CloudEvent specification for queue callbacks
 */
export interface CloudEvent<T = unknown> {
  type: string;
  source: string;
  id: string;
  datacontenttype: string;
  data: T;
  time?: string;
  specversion?: string;
}

/**
 * Configuration object with handlers for different topics and consumer groups
 */
type CallbackHandlers = {
  [topicName: string]: { [consumerGroup: string]: MessageHandler };
};

/**
 * Parsed callback request information
 */
export type ParsedCallbackRequest = {
  queueName: string;
  consumerGroup: string;
  messageId: string;
};

/**
 * Validate wildcard pattern according to rules:
 * - * may only appear once
 * - * may only appear at the end of the topic name
 */
function validateWildcardPattern(pattern: string): boolean {
  const firstIndex = pattern.indexOf("*");
  const lastIndex = pattern.lastIndexOf("*");

  // Rule 1: * may only appear once (first and last index should be the same)
  if (firstIndex !== lastIndex) {
    return false;
  }

  // If no asterisk found, this is not a wildcard pattern
  if (firstIndex === -1) {
    return false;
  }

  // Rule 2: * may only appear at the end (index should equal length - 1)
  if (firstIndex !== pattern.length - 1) {
    return false;
  }

  return true;
}

/**
 * Check if a topic name matches a wildcard pattern
 */
export function matchesWildcardPattern(
  topicName: string,
  pattern: string,
): boolean {
  // No need to validate here since patterns are pre-validated at setup time
  const prefix = pattern.slice(0, -1); // Remove the trailing *
  return topicName.startsWith(prefix);
}

/**
 * Find handler for a topic name, supporting wildcard patterns
 */
function findTopicHandler(
  queueName: string,
  handlers: CallbackHandlers,
): { [consumerGroup: string]: MessageHandler } | null {
  // First, try exact match
  const exactHandler = handlers[queueName];
  if (exactHandler) {
    return exactHandler;
  }

  // Then try wildcard patterns (all patterns are pre-validated)
  for (const pattern in handlers) {
    if (pattern.includes("*") && matchesWildcardPattern(queueName, pattern)) {
      return handlers[pattern];
    }
  }

  return null;
}

/**
 * Parse and validate callback request using CloudEvent specification
 *
 * Extracts queue information from CloudEvent format and validates
 * that all required fields are present.
 *
 * @param request The incoming webhook request
 * @returns Parsed queue information
 * @throws Error if required fields are missing
 *
 * @example
 * ```typescript
 * // In Next.js API route
 * export async function POST(request: Request) {
 *   try {
 *     const { queueName, consumerGroup, messageId } = parseCallback(request);
 *
 *     // Use the parsed information...
 *     await myWorkflow.handleWebhook(queueName, consumerGroup, messageId);
 *
 *     return Response.json({ status: "success" });
 *   } catch (error) {
 *     return Response.json({ error: error.message }, { status: 400 });
 *   }
 * }
 * ```
 */
export async function parseCallback(
  request: Request,
): Promise<ParsedCallbackRequest> {
  // Validate content type header
  const contentType = request.headers.get("content-type");
  if (!contentType || !contentType.includes("application/cloudevents+json")) {
    throw new Error(
      "Invalid content type: expected 'application/cloudevents+json'",
    );
  }

  let cloudEvent: CloudEvent;

  try {
    cloudEvent = (await request.json()) as CloudEvent;
  } catch (error) {
    throw new Error("Failed to parse CloudEvent from request body");
  }

  // Validate CloudEvent structure
  if (
    !cloudEvent.type ||
    !cloudEvent.source ||
    !cloudEvent.id ||
    typeof cloudEvent.data !== "object" ||
    cloudEvent.data == null
  ) {
    throw new Error("Invalid CloudEvent: missing required fields");
  }

  // Validate CloudEvent type
  if (cloudEvent.type !== "com.vercel.queue.v1beta") {
    throw new Error(
      `Invalid CloudEvent type: expected 'com.vercel.queue.v1beta', got '${cloudEvent.type}'`,
    );
  }

  // Check for required data fields before destructuring
  const missingFields: string[] = [];
  if (!("queueName" in cloudEvent.data)) missingFields.push("queueName");
  if (!("consumerGroup" in cloudEvent.data))
    missingFields.push("consumerGroup");
  if (!("messageId" in cloudEvent.data)) missingFields.push("messageId");
  if (missingFields.length > 0) {
    throw new Error(
      `Missing required CloudEvent data fields: ${missingFields.join(", ")}`,
    );
  }

  const { messageId, queueName, consumerGroup } = cloudEvent.data as {
    messageId: string;
    queueName: string;
    consumerGroup: string;
  };

  return {
    queueName,
    consumerGroup,
    messageId,
  };
}

/**
 * Simplified queue callback handler for Next.js route handlers
 *
 * Automatically extracts queue information from CloudEvent format
 * and routes to the appropriate handler based on topic and consumer group.
 *
 * @param handlers Object with topic-specific handlers organized by consumer groups
 * @returns A Next.js route handler function
 *
 * @example
 * ```typescript
 * // Single topic with multiple consumer groups
 * export const POST = handleCallback({
 *   "image-processing": {
 *     "compress": (message, metadata) => console.log("Compressing image", message),
 *     "resize": (message, metadata) => console.log("Resizing image", message),
 *   }
 * });
 *
 * // Multiple topics with consumer groups
 * export const POST = handleCallback({
 *   "user-events": {
 *     "welcome": (user, metadata) => console.log("Welcoming user", user),
 *     "analytics": (user, metadata) => console.log("Tracking user", user),
 *   },
 *   "order-events": {
 *     "fulfillment": (order, metadata) => console.log("Fulfilling order", order),
 *     "notifications": (order, metadata) => console.log("Notifying order", order),
 *   }
 * });
 * ```
 */
export function handleCallback(
  handlers: CallbackHandlers,
): (request: Request) => Promise<Response> {
  // Validate all wildcard patterns at setup time
  for (const topicPattern in handlers) {
    if (topicPattern.includes("*")) {
      if (!validateWildcardPattern(topicPattern)) {
        throw new Error(
          `Invalid wildcard pattern "${topicPattern}": * may only appear once and must be at the end of the topic name`,
        );
      }
    }
  }

  const routeHandler = async (request: Request): Promise<Response> => {
    try {
      // Parse the callback request
      const { queueName, consumerGroup, messageId } =
        await parseCallback(request);

      // Find the topic handler
      const topicHandler = findTopicHandler(queueName, handlers);

      if (!topicHandler) {
        const availableTopics = Object.keys(handlers).join(", ");
        return Response.json(
          {
            error: `No handler found for topic: ${queueName}`,
            availableTopics,
          },
          { status: 404 },
        );
      }

      // Find the consumer group handler
      const consumerGroupHandler = topicHandler[consumerGroup];

      if (!consumerGroupHandler) {
        const availableGroups = Object.keys(topicHandler).join(", ");
        return Response.json(
          {
            error: `No handler found for consumer group "${consumerGroup}" in topic "${queueName}".`,
            availableGroups,
          },
          { status: 404 },
        );
      }

      // Create client and process the message
      const client = new QueueClient();
      const topic = new Topic(client, queueName);
      const cg = topic.consumerGroup(consumerGroup);

      await cg.consume(consumerGroupHandler, { messageId });

      return Response.json({ status: "success" });
    } catch (error) {
      console.error("Queue callback error:", error);

      // Handle parsing errors with appropriate status codes
      if (
        error instanceof Error &&
        (error.message.includes("Missing required CloudEvent data fields") ||
          error.message.includes("Invalid CloudEvent") ||
          error.message.includes("Invalid CloudEvent type") ||
          error.message.includes("Invalid content type") ||
          error.message.includes("Failed to parse CloudEvent"))
      ) {
        return Response.json({ error: error.message }, { status: 400 });
      }

      return Response.json(
        { error: "Failed to process queue message" },
        { status: 500 },
      );
    }
  };

  // Register route handler and mappings in development mode
  if (isDevMode()) {
    registerDevRouteHandler(routeHandler, handlers);
  }

  return routeHandler;
}
