// NOT SAFE TO CACHE
export function getVercelOidcToken(): string | null {
  const SYMBOL_FOR_REQ_CONTEXT = Symbol.for("@vercel/request-context");
  const fromSymbol = globalThis as any;
  const context = fromSymbol[SYMBOL_FOR_REQ_CONTEXT]?.get?.() ?? {};

  const token =
    context.headers?.["x-vercel-oidc-token"] ?? process.env.VERCEL_OIDC_TOKEN;

  if (!token) {
    return null;
  }

  return token;
}
