import { QueueClient } from "./client";
import { JsonTransport } from "./transports";
import type {
  ConsumerGroupOptions,
  Message,
  MessageHandler,
  Transport,
} from "./types";
import { isDevMode, scheduleDevTimeout } from "./dev";

/**
 * Options for the consume method
 */
export interface ConsumeOptions {
  /** The specific message ID to consume (if not provided, consumes next available message) */
  messageId?: string;
  /** Whether to skip downloading the payload (only allowed when messageId is provided) */
  skipPayload?: boolean;
}

/**
 * A ConsumerGroup represents a named group of consumers that process messages from a topic
 */
export class ConsumerGroup<T = unknown> {
  private client: QueueClient;
  private topicName: string;
  private consumerGroupName: string;
  private visibilityTimeout: number;
  private refreshInterval: number;
  private transport: Transport<T>;

  /**
   * Create a new ConsumerGroup instance
   * @param client QueueClient instance to use for API calls
   * @param topicName Name of the topic to consume from
   * @param consumerGroupName Name of the consumer group
   * @param options Optional configuration
   */
  constructor(
    client: QueueClient,
    topicName: string,
    consumerGroupName: string,
    options: ConsumerGroupOptions<T> = {},
  ) {
    this.client = client;
    this.topicName = topicName;
    this.consumerGroupName = consumerGroupName;
    this.visibilityTimeout = options.visibilityTimeoutSeconds || 30; // 30 seconds default
    this.refreshInterval = options.refreshInterval || 10; // 10 seconds default
    this.transport = options.transport || new JsonTransport<T>();
  }

  /**
   * Starts a background loop that periodically extends the visibility timeout for a message.
   * This prevents the message from becoming visible to other consumers while it's being processed.
   *
   * The extension loop runs every `refreshInterval` seconds and updates the message's
   * visibility timeout to `visibilityTimeout` seconds from the current time.
   *
   * @param messageId - The unique identifier of the message to extend visibility for
   * @param ticket - The receipt ticket that proves ownership of the message
   * @returns A function that when called will stop the extension loop
   *
   * @remarks
   * - The first extension attempt occurs after `refreshInterval` seconds, not immediately
   * - If an extension fails, the loop terminates with an error logged to console
   * - The returned stop function is idempotent - calling it multiple times is safe
   * - By default, the stop function returns immediately without waiting for in-flight
   * - Pass `true` to the stop function to wait for any in-flight extension to complete
   */
  private startVisibilityExtension(
    messageId: string,
    ticket: string,
  ): (waitForCompletion?: boolean) => Promise<void> {
    let isRunning = true;
    let resolveLifecycle: () => void;
    let timeoutId: NodeJS.Timeout | null = null;

    // Promise that tracks the actual termination of the extension loop
    const lifecyclePromise = new Promise<void>((resolve) => {
      resolveLifecycle = resolve;
    });

    const extend = async (): Promise<void> => {
      // Check if we should stop before attempting extension
      if (!isRunning) {
        resolveLifecycle();
        return;
      }

      try {
        // Extend the visibility timeout for another period
        await this.client.changeVisibility({
          queueName: this.topicName,
          consumerGroup: this.consumerGroupName,
          messageId,
          ticket,
          visibilityTimeoutSeconds: this.visibilityTimeout,
        });

        // Schedule next extension if still running
        if (isRunning) {
          timeoutId = setTimeout(() => extend(), this.refreshInterval * 1000);
        } else {
          // Signal that the loop has terminated after successful extension
          resolveLifecycle();
        }
      } catch (error) {
        // Log error and terminate the loop on failure
        console.error(
          `Failed to extend visibility for message ${messageId}:`,
          error,
        );
        resolveLifecycle();
      }
    };

    // Schedule the first extension attempt
    timeoutId = setTimeout(() => extend(), this.refreshInterval * 1000);

    // Return a function to stop the extension loop
    return async (waitForCompletion: boolean = false) => {
      // Signal the loop to stop
      isRunning = false;

      // Cancel any pending timeout to avoid unnecessary waiting
      if (timeoutId) {
        clearTimeout(timeoutId);
        timeoutId = null;
      }

      // Only wait for in-flight operations if explicitly requested
      if (waitForCompletion) {
        // Wait for the loop to actually terminate
        // This ensures any in-progress extension completes or fails
        await lifecyclePromise;
      } else {
        // Resolve the lifecycle immediately for any waiting operations
        // This allows immediate return without waiting for in-flight extensions
        resolveLifecycle();
      }
    };
  }

  /**
   * Process a single message with the given handler
   * @param message The message to process
   * @param handler Function to process the message
   */
  private async processMessage<TPayload>(
    message: Message<TPayload>,
    handler: MessageHandler<TPayload>,
  ): Promise<void> {
    const stopExtension = this.startVisibilityExtension(
      message.messageId,
      message.ticket,
    );

    try {
      const result = await handler(message.payload, {
        messageId: message.messageId,
        deliveryCount: message.deliveryCount,
        createdAt: message.createdAt,
        topicName: this.topicName,
        consumerGroup: this.consumerGroupName,
      });
      // Stop extensions immediately - we don't need to wait for in-flight extensions
      // since we're about to delete or update the message visibility anyway
      await stopExtension();

      if (result && "timeoutSeconds" in result) {
        // Handle timeout request - set new visibility timeout instead of deleting
        await this.client.changeVisibility({
          queueName: this.topicName,
          consumerGroup: this.consumerGroupName,
          messageId: message.messageId,
          ticket: message.ticket,
          visibilityTimeoutSeconds: result.timeoutSeconds,
        });

        // In development mode, schedule re-trigger after timeout period
        if (isDevMode()) {
          scheduleDevTimeout(
            this.topicName,
            message.messageId,
            result.timeoutSeconds,
          );
        }
      } else {
        // Normal completion - delete the message
        await this.client.deleteMessage({
          queueName: this.topicName,
          consumerGroup: this.consumerGroupName,
          messageId: message.messageId,
          ticket: message.ticket,
        });
      }
    } catch (error) {
      // Stop extensions immediately on error - fail fast without waiting
      // for any in-flight extension attempts
      await stopExtension();

      // Clean up the message payload if the transport supports it and payload exists
      // Only call finalize for non-void payloads since transport is typed for T, not void
      if (
        this.transport.finalize &&
        message.payload !== undefined &&
        message.payload !== null
      ) {
        try {
          // Safe cast: when processMessage<T> is called, TPayload is T
          // when processMessage<void> is called, payload is undefined so this won't execute
          await this.transport.finalize(message.payload as T);
        } catch (finalizeError) {
          console.warn("Failed to finalize message payload:", finalizeError);
        }
      }

      throw error;
    }
  }

  /**
   * Consume the next available message from the queue
   * @param handler Function to process the message
   * @returns Promise that resolves when the message is processed
   * @throws All the same errors as the underlying client methods
   */
  async consume(handler: MessageHandler<T>): Promise<void>;

  /**
   * Consume a specific message by its ID with full payload
   * @param handler Function to process the message
   * @param options Consume options with messageId specified
   * @returns Promise that resolves when the message is processed
   * @throws All the same errors as the underlying client methods
   */
  async consume(
    handler: MessageHandler<T>,
    options: { messageId: string; skipPayload?: false | undefined },
  ): Promise<void>;

  /**
   * Consume a specific message by its ID without downloading the payload (metadata only)
   * @param handler Function to process the message metadata (payload will be void)
   * @param options Consume options with messageId and skipPayload specified
   * @returns Promise that resolves when the message is processed
   * @throws All the same errors as the underlying client methods
   */
  async consume(
    handler: MessageHandler<void>,
    options: { messageId: string; skipPayload: true },
  ): Promise<void>;

  async consume(
    handler: MessageHandler<T> | MessageHandler<void>,
    options?: ConsumeOptions,
  ): Promise<void> {
    if (options?.messageId) {
      // Specific message mode
      if (options.skipPayload) {
        // Skip payload - like old handleMessage
        const response = await this.client.receiveMessageById(
          {
            queueName: this.topicName,
            consumerGroup: this.consumerGroupName,
            messageId: options.messageId,
            visibilityTimeoutSeconds: this.visibilityTimeout,
            skipPayload: true,
          },
          this.transport,
        );
        await this.processMessage<void>(
          response.message,
          handler as MessageHandler<void>,
        );
      } else {
        // With payload - like old receiveMessage
        const response = await this.client.receiveMessageById(
          {
            queueName: this.topicName,
            consumerGroup: this.consumerGroupName,
            messageId: options.messageId,
            visibilityTimeoutSeconds: this.visibilityTimeout,
          },
          this.transport,
        );
        await this.processMessage<T>(
          response.message,
          handler as MessageHandler<T>,
        );
      }
    } else {
      // Next message mode - like old receiveNextMessage
      let messageFound = false;

      for await (const message of this.client.receiveMessages<T>(
        {
          queueName: this.topicName,
          consumerGroup: this.consumerGroupName,
          visibilityTimeoutSeconds: this.visibilityTimeout,
          limit: 1,
        },
        this.transport,
      )) {
        messageFound = true;
        await this.processMessage<T>(message, handler as MessageHandler<T>);
        break; // Process only one message
      }

      // If we get here without finding a message, the async generator
      // should have already thrown QueueEmptyError, but just in case
      if (!messageFound) {
        throw new Error("No messages available");
      }
    }
  }

  /**
   * Get the consumer group name
   */
  get name(): string {
    return this.consumerGroupName;
  }

  /**
   * Get the topic name this consumer group is subscribed to
   */
  get topic(): string {
    return this.topicName;
  }
}
