import { QueueClient } from "./client";
import { ConsumerGroup } from "./consumer-group";
import { JsonTransport } from "./transports";
import type { ConsumerGroupOptions, PublishOptions, Transport } from "./types";
import { isDevMode, triggerDevCallbacks } from "./dev";

/**
 * A Topic represents a named channel for publishing messages in a pub/sub pattern
 */
export class Topic<T = unknown> {
  private client: QueueClient;
  private topicName: string;
  private transport: Transport<T>;

  /**
   * Create a new Topic instance
   * @param client QueueClient instance to use for API calls
   * @param topicName Name of the topic to work with
   * @param transport Optional serializer/deserializer for the payload (defaults to JSON)
   */
  constructor(
    client: QueueClient,
    topicName: string,
    transport?: Transport<T>,
  ) {
    this.client = client;
    this.topicName = topicName;
    this.transport = transport || new JsonTransport<T>();
  }

  /**
   * Publish a message to the topic
   * @param payload The data to publish
   * @param options Optional publish options
   * @returns An object containing the message ID
   * @throws {BadRequestError} When request parameters are invalid
   * @throws {UnauthorizedError} When authentication fails
   * @throws {ForbiddenError} When access is denied (environment mismatch)
   * @throws {InternalServerError} When server encounters an error
   */
  async publish(
    payload: T,
    options?: PublishOptions,
  ): Promise<{ messageId: string }> {
    const result = await this.client.sendMessage<T>(
      {
        queueName: this.topicName,
        payload,
        idempotencyKey: options?.idempotencyKey,
        retentionSeconds: options?.retentionSeconds,
        deploymentId: options?.deploymentId,
      },
      this.transport,
    );

    // In development mode, automatically trigger registered callbacks after 1s
    if (isDevMode()) {
      triggerDevCallbacks(this.topicName, result.messageId);
    }

    return { messageId: result.messageId };
  }

  /**
   * Create a consumer group for this topic
   * @param consumerGroupName Name of the consumer group
   * @param options Optional configuration for the consumer group
   * @returns A ConsumerGroup instance
   */
  consumerGroup<U = T>(
    consumerGroupName: string,
    options?: ConsumerGroupOptions<U>,
  ): ConsumerGroup<U> {
    // If no transport is provided in options, use the topic's transport if types match
    const consumerOptions: ConsumerGroupOptions<U> = {
      ...options,
      transport:
        options?.transport || (this.transport as unknown as Transport<U>),
    };

    return new ConsumerGroup<U>(
      this.client,
      this.topicName,
      consumerGroupName,
      consumerOptions,
    );
  }

  /**
   * Get the topic name
   */
  get name(): string {
    return this.topicName;
  }

  /**
   * Get the transport used by this topic
   */
  get serializer(): Transport<T> {
    return this.transport;
  }
}
