/**
 * Development mode utilities for local queue compatibility
 * This file contains helpers that enable local callback triggering in development mode
 *
 * Key behaviors:
 * - Exact topic matches: Only the latest handler is kept for each topic:consumerGroup pair
 * - Wildcard patterns: All handlers are kept to allow multiple features to handle the same pattern
 * - Memory management: Uses WeakRef for wildcard handlers to prevent memory leaks
 */
import type { CloudEvent } from "./callback";
import { matchesWildcardPattern } from "./callback";

/**
 * Store route handlers keyed by topic:consumerGroup
 * This ensures we keep the latest handler for each topic:consumerGroup pair
 */
const devRouteHandlers: Map<
  string,
  {
    routeHandler: (request: Request) => Promise<Response>;
    topicPattern: string; // Store the original pattern for wildcard matching
  }
> = new Map();

/**
 * Store wildcard route handlers separately to allow multiple handlers for the same pattern
 * Uses WeakRef to prevent memory leaks when route handlers are garbage collected
 */
const wildcardRouteHandlers: Map<
  string, // pattern:consumerGroup key
  WeakRef<(request: Request) => Promise<Response>>[]
> = new Map();

/**
 * Track which route handler manages which topic:consumerGroup pairs
 * This helps us clean up old entries when a route handler is replaced
 */
const routeHandlerKeys: WeakMap<
  (request: Request) => Promise<Response>,
  Set<string>
> = new WeakMap();

/**
 * Clean up dead WeakRefs from a specific pattern's handler list
 */
function cleanupDeadRefs(
  key: string,
  refs: WeakRef<(request: Request) => Promise<Response>>[],
): void {
  const aliveRefs = refs.filter((ref) => ref.deref() !== undefined);
  if (aliveRefs.length === 0) {
    wildcardRouteHandlers.delete(key);
  } else if (aliveRefs.length < refs.length) {
    wildcardRouteHandlers.set(key, aliveRefs);
  }
}

/**
 * Check if running in development mode
 */
export function isDevMode(): boolean {
  return process.env.NODE_ENV === "development";
}

/**
 * Register the route handler and topic mappings for development mode
 * @internal
 */
export function registerDevRouteHandler(
  routeHandler: (request: Request) => Promise<Response>,
  handlers: { [topicName: string]: { [consumerGroup: string]: unknown } },
): void {
  // First, clean up any existing entries for topics/groups that this route handler previously managed
  // but are no longer in the new handlers configuration
  const existingKeys = routeHandlerKeys.get(routeHandler);
  if (existingKeys) {
    const newKeys = new Set<string>();

    // Build set of new keys
    for (const topicName in handlers) {
      for (const consumerGroup in handlers[topicName]) {
        newKeys.add(`${topicName}:${consumerGroup}`);
      }
    }

    // Remove old keys that are no longer in the new configuration
    for (const key of existingKeys) {
      if (!newKeys.has(key)) {
        const [topicPattern] = key.split(":");
        if (topicPattern.includes("*")) {
          // For wildcards, remove this specific route handler from the list
          const refs = wildcardRouteHandlers.get(key);
          if (refs) {
            const filteredRefs = refs.filter(
              (ref) => ref.deref() !== routeHandler,
            );
            if (filteredRefs.length === 0) {
              wildcardRouteHandlers.delete(key);
            } else {
              wildcardRouteHandlers.set(key, filteredRefs);
            }
          }
        } else {
          devRouteHandlers.delete(key);
        }
      }
    }
  }

  // Track keys for this route handler
  const keys = new Set<string>();

  // Register each topic:consumerGroup pair
  for (const topicName in handlers) {
    for (const consumerGroup in handlers[topicName]) {
      const key = `${topicName}:${consumerGroup}`;
      keys.add(key);

      if (topicName.includes("*")) {
        // For wildcards, add to the wildcard handlers list
        const weakRef = new WeakRef(routeHandler);
        const existing = wildcardRouteHandlers.get(key) || [];
        // Clean up dead refs on-demand
        cleanupDeadRefs(key, existing);
        // Get the cleaned refs and add the new one
        const cleanedRefs = wildcardRouteHandlers.get(key) || [];
        cleanedRefs.push(weakRef);
        wildcardRouteHandlers.set(key, cleanedRefs);
      } else {
        // For exact matches, keep the existing behavior
        devRouteHandlers.set(key, {
          routeHandler,
          topicPattern: topicName,
        });
      }
    }
  }

  // Update the tracking for this route handler
  routeHandlerKeys.set(routeHandler, keys);
}

/**
 * Find all consumer groups for a topic across all registered handlers
 * @internal
 */
export function findDevConsumerGroups(topicName: string): Set<string> | null {
  const consumerGroups = new Set<string>();

  // Check exact match handlers
  for (const [key, { topicPattern }] of devRouteHandlers.entries()) {
    const [pattern, consumerGroup] = key.split(":");

    // Only exact matches here
    if (pattern === topicName) {
      consumerGroups.add(consumerGroup);
    }
  }

  // Check wildcard handlers
  for (const [key, refs] of wildcardRouteHandlers.entries()) {
    const [pattern, consumerGroup] = key.split(":");

    if (matchesWildcardPattern(topicName, pattern)) {
      // Clean up dead refs on-demand
      cleanupDeadRefs(key, refs);
      // Check if there are still alive handlers after cleanup
      const cleanedRefs = wildcardRouteHandlers.get(key);
      if (cleanedRefs && cleanedRefs.length > 0) {
        consumerGroups.add(consumerGroup);
      }
    }
  }

  return consumerGroups.size > 0 ? consumerGroups : null;
}

/**
 * Find all route handlers that should receive a message for a topic
 * Returns a map of route handler to consumer groups it should process
 */
function findRouteHandlersForTopic(
  topicName: string,
): Map<(request: Request) => Promise<Response>, Set<string>> {
  const handlersMap = new Map<
    (request: Request) => Promise<Response>,
    Set<string>
  >();

  // Check exact match handlers
  for (const [
    key,
    { routeHandler, topicPattern },
  ] of devRouteHandlers.entries()) {
    const [_, consumerGroup] = key.split(":");

    // Only exact matches here (wildcards are handled separately)
    if (topicPattern === topicName) {
      if (!handlersMap.has(routeHandler)) {
        handlersMap.set(routeHandler, new Set());
      }
      handlersMap.get(routeHandler)!.add(consumerGroup);
    }
  }

  // Check wildcard handlers
  for (const [key, refs] of wildcardRouteHandlers.entries()) {
    const [pattern, consumerGroup] = key.split(":");

    if (matchesWildcardPattern(topicName, pattern)) {
      // Clean up dead refs on-demand
      cleanupDeadRefs(key, refs);
      // Get the cleaned refs and add all alive route handlers
      const cleanedRefs = wildcardRouteHandlers.get(key) || [];
      for (const ref of cleanedRefs) {
        const routeHandler = ref.deref();
        if (routeHandler) {
          if (!handlersMap.has(routeHandler)) {
            handlersMap.set(routeHandler, new Set());
          }
          handlersMap.get(routeHandler)!.add(consumerGroup);
        }
      }
    }
  }

  return handlersMap;
}

/**
 * Create a mock CloudEvent request for development mode
 * @internal
 */
function createMockCloudEventRequest(
  topicName: string,
  consumerGroup: string,
  messageId: string,
): Request {
  const cloudEvent: CloudEvent = {
    type: "com.vercel.queue.v1beta",
    source: `/topic/${topicName}/consumer/${consumerGroup}`,
    id: messageId,
    datacontenttype: "application/json",
    data: {
      messageId,
      queueName: topicName,
      consumerGroup,
    },
    time: new Date().toISOString(),
    specversion: "1.0",
  };

  return new Request("https://localhost/api/queue/callback", {
    method: "POST",
    headers: {
      "Content-Type": "application/cloudevents+json",
    },
    body: JSON.stringify(cloudEvent),
  });
}

/**
 * Default delay for triggering dev callbacks (in milliseconds)
 */
const DEV_CALLBACK_DELAY = 1000;

/**
 * Schedule a dev mode timeout re-trigger for a message
 * @internal
 */
export function scheduleDevTimeout(
  topicName: string,
  messageId: string,
  timeoutSeconds: number,
): void {
  console.log(
    `[Dev Mode] Message ${messageId} timed out for ${timeoutSeconds}s, will re-trigger`,
  );

  // Schedule re-trigger after the timeout period + 1s buffer to avoid race conditions
  setTimeout(
    () => {
      console.log(
        `[Dev Mode] Re-triggering callback for timed-out message ${messageId}`,
      );
      triggerDevCallbacks(topicName, messageId);
    },
    timeoutSeconds * 1000 + DEV_CALLBACK_DELAY,
  );
}

/**
 * Trigger development mode callbacks for a topic
 * @internal
 */
export function triggerDevCallbacks(
  topicName: string,
  messageId: string,
): void {
  const handlersMap = findRouteHandlersForTopic(topicName);

  if (handlersMap.size === 0) {
    return; // No handlers for this topic
  }

  const consumerGroups = Array.from(
    new Set(
      Array.from(handlersMap.values()).flatMap((groups) => Array.from(groups)),
    ),
  );

  console.log(
    `[Dev Mode] Triggering local callbacks for topic "${topicName}"  consumers: ${consumerGroups.join(", ")}`,
  );

  setTimeout(async () => {
    // Call each route handler with its consumer groups
    for (const [routeHandler, consumerGroups] of handlersMap.entries()) {
      for (const consumerGroup of consumerGroups) {
        try {
          const request = createMockCloudEventRequest(
            topicName,
            consumerGroup,
            messageId,
          );

          const response = await routeHandler(request);

          if (response.ok) {
            try {
              const responseData = (await response.json()) as {
                status?: string;
              };
              if (responseData.status === "success") {
                console.log(
                  `[Dev Mode] Message processed for ${topicName}/${consumerGroup}`,
                );
              }
            } catch (jsonError) {
              console.error(
                `[Dev Mode] Failed to parse success response for ${topicName}/${consumerGroup}:`,
                jsonError,
              );
            }
          } else {
            try {
              const errorData = (await response.json()) as { error?: string };
              console.error(
                `[Dev Mode] Failed to process message for ${topicName}/${consumerGroup}:`,
                errorData.error || response.statusText,
              );
            } catch (jsonError) {
              console.error(
                `[Dev Mode] Failed to process message for ${topicName}/${consumerGroup}:`,
                response.statusText,
              );
            }
          }
        } catch (error) {
          console.error(
            `[Dev Mode] Error triggering callback for ${topicName}/${consumerGroup}:`,
            error,
          );
        }
      }
    }
  }, DEV_CALLBACK_DELAY);
}

/**
 * Clear all dev handlers - for testing only
 * @internal
 */
function clearDevHandlers(): void {
  devRouteHandlers.clear();
  wildcardRouteHandlers.clear();
}

// Export for testing
if (process.env.NODE_ENV === "test" || process.env.VITEST) {
  (globalThis as any).__clearDevHandlers = clearDevHandlers;
}
