import { QueueClient } from "./client";
import type { ConsumeOptions } from "./consumer-group";
import { Topic } from "./topic";
import { JsonTransport } from "./transports";
import type {
  ConsumerGroupOptions,
  MessageHandler,
  PublishOptions,
  Transport,
} from "./types";
import { isDevMode, triggerDevCallbacks } from "./dev";

/**
 * Options for the send function
 */
export interface SendOptions<T = unknown> extends PublishOptions {
  /**
   * Serializer/deserializer for the payload
   * @default JsonTransport instance
   */
  transport?: Transport<T>;
}

/**
 * Send a message to a topic (shorthand for topic creation and publishing)
 * Uses the default QueueClient with automatic OIDC token detection
 * @param topicName Name of the topic to send to
 * @param payload The data to send
 * @param options Optional send options including transport and publish settings
 * @returns Promise with the message ID
 * @throws {BadRequestError} When request parameters are invalid
 * @throws {UnauthorizedError} When authentication fails
 * @throws {ForbiddenError} When access is denied (environment mismatch)
 * @throws {InternalServerError} When server encounters an error
 */
export async function send<T = unknown>(
  topicName: string,
  payload: T,
  options?: SendOptions<T>,
): Promise<{ messageId: string }> {
  const transport = options?.transport || new JsonTransport<T>();
  const client = new QueueClient();

  const result = await client.sendMessage<T>(
    {
      queueName: topicName,
      payload,
      idempotencyKey: options?.idempotencyKey,
      retentionSeconds: options?.retentionSeconds,
      deploymentId: options?.deploymentId,
    },
    transport,
  );

  // In development mode, automatically trigger registered callbacks after 1s
  if (isDevMode()) {
    triggerDevCallbacks(topicName, result.messageId);
  }

  return { messageId: result.messageId };
}

/**
 * Options for the receive function
 */
export interface ReceiveOptions<T = unknown>
  extends ConsumerGroupOptions<T>,
    ConsumeOptions {}

/**
 * Receive a message from a topic (shorthand for topic and consumer group creation)
 * Uses the default QueueClient with automatic OIDC token detection
 * @param topicName Name of the topic to receive from
 * @param consumerGroup Name of the consumer group
 * @param handler Function to process the message
 * @returns Promise that resolves when the message is processed
 * @throws All the same errors as the underlying client methods
 */
export async function receive<T = unknown>(
  topicName: string,
  consumerGroup: string,
  handler: MessageHandler<T>,
  options?: ReceiveOptions<T>,
): Promise<void>;

/**
 * Receive a specific message by its ID with full payload
 * @param topicName Name of the topic to receive from
 * @param consumerGroup Name of the consumer group
 * @param handler Function to process the message
 * @param options Receive options with messageId specified
 * @returns Promise that resolves when the message is processed
 * @throws All the same errors as the underlying client methods
 */
export async function receive<T = unknown>(
  topicName: string,
  consumerGroup: string,
  handler: MessageHandler<T>,
  options: ReceiveOptions<T> & {
    messageId: string;
    skipPayload?: false | undefined;
  },
): Promise<void>;

/**
 * Receive a specific message by its ID without downloading the payload (metadata only)
 * @param topicName Name of the topic to receive from
 * @param consumerGroup Name of the consumer group
 * @param handler Function to process the message metadata (payload will be void)
 * @param options Receive options with messageId and skipPayload specified
 * @returns Promise that resolves when the message is processed
 * @throws All the same errors as the underlying client methods
 */
export async function receive<T = unknown>(
  topicName: string,
  consumerGroup: string,
  handler: MessageHandler<void>,
  options: ReceiveOptions<T> & { messageId: string; skipPayload: true },
): Promise<void>;

export async function receive<T = unknown>(
  topicName: string,
  consumerGroup: string,
  handler: MessageHandler<T> | MessageHandler<void>,
  options?: ReceiveOptions<T>,
): Promise<void> {
  const transport = options?.transport || new JsonTransport<T>();

  // Create topic with transport directly
  const client = new QueueClient();
  const topic = new Topic<T>(client, topicName, transport);

  // Create consumer group with options (excluding consume-specific options)
  const { messageId, skipPayload, ...consumerGroupOptions } = options || {};
  const consumer = topic.consumerGroup(consumerGroup, consumerGroupOptions);

  // Call consume with the appropriate overload based on options
  if (messageId) {
    if (skipPayload) {
      // skipPayload: true case
      return consumer.consume(handler as MessageHandler<void>, {
        messageId,
        skipPayload: true,
      });
    } else {
      // messageId with payload case
      return consumer.consume(handler as MessageHandler<T>, { messageId });
    }
  } else {
    // No options case - next available message
    return consumer.consume(handler as MessageHandler<T>);
  }
}
